import React, { Component } from 'react'
import classNames from 'classnames'

import './Button.scss'

import Icon from '../redesign/Icon'

export enum ButtonType {
	Primary = 'primary',
	Secondary = 'secondary',
	Dangerous = 'dangerous',
	Neutral = 'neutral',
	White = 'white',
}

export enum ButtonIconPosition {
	Left = 'left',
	Right = 'right',
}

export interface IButtonProps {
	mode: ButtonType
	iconPosition?: ButtonIconPosition
	iconSize?: number
	onClick?: () => void
	disabled?: boolean
	className?: string
	clipboardData?: any
	inverted?: boolean
	thin?: boolean
	thiner?: boolean
	small?: boolean
	wide?: boolean
	transparent?: boolean
	iconName?: string | null
	children?: any
	style?: React.CSSProperties
	title?: any
	id?: string
}

export default class Button extends Component<IButtonProps> {
	private component: HTMLButtonElement | null = null

	public static defaultProps = {
		mode: ButtonType.Primary,
		iconPosition: ButtonIconPosition.Left,
		iconSize: 16,
	}

	public getComponent = () => this.component

	public render() {
		const {
			clipboardData,
			iconName,
			iconPosition,
			iconSize,
			inverted,
			disabled,
			mode,
			className,
			onClick,
			thin,
			thiner,
			transparent,
			small,
			wide,
			...rest
		} = this.props

		const leftIconActive = iconName && iconPosition === 'left'
		const rightIconActive = iconName && iconPosition === 'right'

		const defaultClasses = [
			'button-v2',
			leftIconActive ? 'button-v2--with-icon-left' : '',
			rightIconActive ? 'button-v2--with-icon-right' : '',
			{ 'button-v2--primary': mode === ButtonType.Primary && !inverted },
			{ 'button-v2--primary--inverted': mode === ButtonType.Primary && inverted },
			{ 'button-v2--secondary': mode === ButtonType.Secondary && !inverted },
			{ 'button-v2--secondary--inverted': mode === ButtonType.Secondary && inverted },
			{ 'button-v2--dangerous': mode === ButtonType.Dangerous },
			{ 'button-v2--dangerous--inverted': mode === ButtonType.Dangerous && inverted },
			{ 'button-v2--neutral': mode === ButtonType.Neutral },
			{ 'button-v2--neutral--inverted': mode === ButtonType.Neutral && inverted },
			{ 'button-v2--white': mode === ButtonType.White },
			{ 'button-v2--disabled': disabled },
			{ 'button-v2--thin': thin },
			{ 'button-v2--thiner': thiner },
			{ 'button-v2--small': small },
			{ 'button-v2--wide': wide },
			{ 'button-v2--transparent': transparent },
		]
		return (
			<button
				ref={c => {
					this.component = c
				}}
				{...(clipboardData ? { 'data-clipboard-text': clipboardData } : {})}
				onClick={onClick}
				disabled={disabled}
				className={classNames(defaultClasses, className)}
				{...rest}
			>
				{leftIconActive && <Icon name={iconName as string} size={iconSize} />}
				{this.props.children && <span>{this.props.children}</span>}
				{rightIconActive && <Icon name={iconName as string} size={iconSize} />}
			</button>
		)
	}
}
