import React, { StatelessComponent, CSSProperties } from 'react'
import classNames from 'classnames'

import './Icon.scss'

export interface IIconProps {
	name: string
	size?: number
	className?: string
	onClick?: () => void
	style?: CSSProperties
	title?: string
}

const Icon: StatelessComponent<IIconProps> = props => {
	const { onClick, className, name, size, style, title } = props

	return (
		<div
			style={style || {}}
			className={classNames('icon-wrapper', className)}
			onClick={onClick || undefined}
			title={title || undefined}
		>
			<i className={classNames('icon', `icon-${name}`)} style={{ fontSize: size || 14 }} />
		</div>
	)
}

export default Icon

